	module.exports = {"data":{"post":{"html":"<p>A TV film crew is in Lüllau, filming at the farm place across the street. Mama says that Heinz Reincke (Germans will know who he is... he looks like the typical North German grandpa, even though he actually lives in Vienna) had to drive 6 times down our street for the camera. The scene being filmed this evening seemed to involve a big white billy goat.</p>\n<p>When our office was on the Hamburg Harbor, I'd see film crews there practicall every week. Our new office, though, is on the wrong part of the Reeperbahn to attract any film crews. The red light district is at the other end.</p>","fields":{"title":"Film at 11","date":"27 Jul 2000","url":"https://papascott.de/2000-07-27-film-at-11/"}}},"pathContext":{"slug":"/2000-07-27-film-at-11/","prev":{"fields":{"title":"Home Alone","date":"26 Jul 2000","slug":"/2000-07-26-home-alone/"}},"next":{"fields":{"title":"Lola","date":"29 Jul 2000","slug":"/2000-07-29-lola/"}}}}

